.flip-container {
  cursor: pointer;
  perspective: 1000;
}
.flip-container:hover .flipper {
  transform: rotateY(180deg);
}
.flip-container .front,
.flip-container .back {
  width: 30rem;
  height: 30rem;
  border-radius: 50%;
}
.flip-container .flipper {
  transition: 1s ease-in-out all;
  transform-style: preserve-3d;
  position: relative;
  width: 30rem;
  height: 30rem;
  background: #fff;
  border-radius: 50%;
  box-shadow: inset 0 0 0 1rem #6673b8;
}
.flip-container .front,
.flip-container .back {
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flip-container .front a,
.flip-container .back a {
  font-weight: 600;
  font-size: 2rem;
  color: #6673b8;
  margin: 2rem 0;
}
.flip-container .front {
  z-index: 2;
  padding: 3rem;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.flip-container .front p {
  font-weight: 600;
  font-size: 2rem;
  position: absolute;
  left: 0;
  right: 0;
  top: 13rem;
  width: 21rem;
  height: 100%;
  margin: 0 auto;
  tex-shadow: 0.1rem 0.1rem 0.3rem #fff;
}
.flip-container .front i {
  position: relative;
  color: #6673b8;
  font-size: 15rem;
  opacity: 0.5;
}
.flip-container .back {
  transform: rotateY(180deg);
}
.flip-container .review {
  display: flex;
  flex-direction: row;
  position: relative;
  overflow: hidden;
  top: -30rem;
  border: 1rem solid #6673b8;
}
.flip-container .review__instagram {
  width: 50%;
  background: #ee7814;
  color: #fff;
  transition: 0.5s ease-in-out all;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  width: 50%;
  height: 100%;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.flip-container .review__instagram i {
  color: #fff;
  font-size: 4rem;
  transition: 0.5s ease-in-out all;
}
.flip-container .review__instagram p {
  opacity: 0;
  transition: 0.5s ease-in-out all;
}
.flip-container .review__instagram:hover {
  background: #fff;
  color: #ee7814;
}
.flip-container .review__instagram:hover p {
  opacity: 1;
}
.flip-container .review__instagram:hover i {
  color: #ee7814;
}
.flip-container .review__vk {
  width: 50%;
  background: #4a76a8;
  color: #fff;
  transition: 0.5s ease-in-out all;
  position: absolute;
  margin: 0 !important;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.flip-container .review__vk i {
  color: #fff;
  font-size: 4rem;
